div[class^='highlight-']
{
  background: #FEF9EB;
  border: 1px solid #e1e4e5;
  margin: 0 0 1.5em 0;
}
.code
{
  background: #FEF9EB;
  border: 1px solid #e1e4e5;
  margin: 0 0 1.5em 0;
}
.code-block-caption
{
  background: #eeeeee;
  position: relative;
  color: #000;
  font: 1.1em var(--font)
  padding: 0.2em 0 0.2em 0.8em;
  border: 1px solid #e1e4e5;
  border-bottom: 0;
}
  .code-block-caption .docutils.literal
  {
    font: 0.8em 'Incosolata',monospace;
    padding: 0.1em .4em;
    white-space: nowrap;
    color: #555;
    margin: 0 0.2em;
    position: relative;
    top: -0.1em;
  }

.highlighttable
{
  background: #FEF9EB;
  padding: 0;
  vertical-align: top;
  font: 0.85em 'Incosolata', monospace;
}
  .highlighttable .code
  {
    border: none;
    margin: 0;
    position: relative;
    overflow: auto;
    max-width: 873px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .highlighttable .linenos
  {
    line-height: 1.5em;
  }
    .highlighttable .linenodiv
    {
      width: 45px;
    }
      .highlighttable .linenodiv pre
      {
        border-right: solid 1px #e6e9ea;
        color: #AAB2AC;
        margin: 0;
        padding: 0 0 0 12px;
      }
  .highlighttable .highlight
  {
    color: #404040;
  }
  .highlight
  {
    padding: 1.5em 1.5em;
    margin: 0;
    white-space: pre;
    line-height: 1.5em;
    overflow: visible;
    position: relative;
  }
    .highlight pre
    {
      position: relative;
      padding: 0;
      margin: 0;
      overflow: auto;
    }


.literal-block-wrapper
{
  clear: both;
}


